.max-width-container {
  box-sizing: border-box;
  max-width: 1400px;
  padding: 24px 0;

  &.full {
    width: 100%;
  }
}

.main-container {
  display: grid;
  grid-template-columns: 24px 1fr 24px;

  > * {
    grid-column: 2;

    &.full {
      grid-column: 1 /-1;
    }
  }
}
