* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Gotham-Black;
  color: #393c41;
}

body {
  width: 100%;
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  line-height: 1;
}

img {
  width: 100%;
  object-fit: cover;
}

p {
  margin: 0 0 1em;
}
